        dataIndex: 'address'
      }, {
        title: '操作',
        dataIndex: '',
        render: (text, record, index) => {
          return `<span class="item">
                    <a href="javascript:;" @click="edit('${record.name}')">编辑</a>
                    <span class="ant-divider"></span>
                    <a href="javascript:;" @click="del('${record.name}')">删除</a>
                  </span>`
        }
      }],
      dataSource: [{
        key: '1',
        name: 'LISAN',
        age: 31,
        address: '西湖区湖底公园1号'
      }, {
        key: '2',
        name: '胡彦祖',
        age: 42,
        address: '西湖区湖底公园2号'
      }, {
        key: '3',
        name: '李大嘴',
        age: 54,
        address: '西湖区湖底公园3号'
      }]
    }
  },

  components: { vTable },

  compiled () {
    // debugger
  },

  methods: {
    edit (name) {
      alert('编辑#名字#' + name)
    },

    del (name) {
       alert('删除#名字#' + name)
    }
  }

}


