import Tooltip from '../../components/base/tooltip'

export default {
  data () {
    return {
      visible: false,
      triggerVisible: false
    }
  },

  components: { Tooltip },

  methods: {
    _onChange (e) {
      this.visible = !e.target.value
    },

    _toggle (visible) {
      this.triggerVisible = visible
    }
  }
}


