import { defaultProps } from '../../utils'
import Tooltip from '../base/tooltip'
import vIcon from '../iconfont'
import vButton from '../button'

export default {
  props: defaultProps({
    prefixCls: 'ant-popover',
    title: '',
    transitionName: '',
    placement: 'top',
    trigger: {
      default: function() {return ['click']}
    },
    overlayStyle: {
      default: function() {return{}}
    },
    onConfirm: () => {},
    onCancel: () => {}
  }),

  components: { Tooltip, vIcon, vButton },

  data () {
    return {
      visible: false
    }
  },

  computed: {
    transitionName () {
      return ({
        top: 'zoom-down',
        bottom: 'zoom-up',
        left: 'zoom-right',
        right: 'zoom-left',
        topLeft: 'zoom-down',
        bottomLeft: 'zoom-up',
        leftTop: 'zoom-right',
        rightTop: 'zoom-left',
        topRight: 'zoom-down',
        bottomRight: 'zoom-up',
        leftBottom: 'zoom-right',
        rightBottom: 'zoom-left'
      })[this.placement]
    }
  },

  methods: {
    _confirm () {
      this.onConfirm()
      this.visible = false
    },

    _cancel () {
      this.onCancel()
      this.visible = false
    },

    _onVisibleChange (value) {
      this.visible = value
    }
  }
}


