import vQueueAnim from '../../components/queue-anim'
import vButton from '../../components/button'
import vCheckbox from '../../components/checkbox'


export default {
  data () {
    return {
      items: ['1', '2', '3'],
      show: true
    }
  },

  components: { vQueueAnim, vButton, vCheckbox },

  methods: {
    _handleClick () {
      this.show = !this.show
    },

    _handleAdd () {
      const len = this.items.length
      this.items.push(`${len}`)
    },

    _handleRemove () {
      this.items.pop()
    }
  }

}


