import Animate from '../../components/base/animate'
import vCheckbox from '../../components/checkbox'

export default {
  data () {
    return {
      show: false
    }
  },

  components: { Animate, vCheckbox },

  methods: {
    _toggle () {
      this.show = !this.show
    }
  }
}


