import vPopconfirm from '../../components/popconfirm'
import vButton from '../../components/button'
import message from '../../components/message'

export default {
  data () {
    return {
      text: '确定要删除这个任务吗？'
    }
  },

  components: { vPopconfirm, vButton },

  methods: {
    _confirm () {
      message.success('点击了确定')
    },

    _cancel () {
      message.error('点击了取消')
    }
  }
}


